.container {
	display: flex;
	flex-direction: row;
	width: 100%;
	height: 100vh;

	align-items: center;
	justify-content: center;
}

.image {
	border: none;
    height: 300px;
    width: 300px;
	border-radius: 100%;

    margin-right: 5%;
}

.text {
	width: 500px;
	max-width: 100%;
	font-family: var(--word-font);
	font-size: 1.5em;
	color: var(--dark-word-color);
}
