.bigContainer {
	position: relative;

	width: 100%;
	height: 10vh;
}

.background {
	position: absolute;
	width: 100%;
	height: 10vh;
	overflow: hidden;
}

.contacts {
	position: relative;
	width: 100%;
	height: 10vh;

	display: flex;
	flex-direction: row;

	justify-content: center;
}

.container {
	min-width: 520px;
	height: 10vh;

	display: flex;
	align-items: center;
	flex-direction: row;
	justify-content: space-evenly;
}

.phone {
	display: flex;
	flex-direction: row;

	align-items: center;
	justify-content: center;

	text-decoration: none;

	color: var(--light-word-color);

    margin-left: -15px;
}
